'use strict';
import { COMPANY_LOGO, UPLOAD } from '@/config/image.js';
import { loanListDetail, faceSumbit, loanBank } from '@/api/todoChild/loan.js';
import { postShop } from '@/api/inShop.js';
import { HOME } from '@/config/router.js';
import { UPLOADPHOTO } from '@/config/api.js';
var _self;
export default {
	data() {
		return {
			imglogo: COMPANY_LOGO,
			imgupload: UPLOAD,
			ageData: [{ name: '营业执照', img: '' }, { name: '公司Logo', img: '' }, { name: '门店照片', img: '' }, { name: '装修案例', img: '' }],
			photoData: [{ name: '装修案例', img: '' }],

			list: {},
			show: false
		};
	},
	methods: {
		_previewImage(image) {
			var imgArr = [];
			imgArr.push(image);
			//预览图片
			uni.previewImage({
				urls: imgArr,
				current: imgArr[0]
			});
		},
		toast(v) {
			uni.showToast({
				title: v,
				duration: 2000,
				icon: 'none'
			});
		},
		toLinkChoose() {
			if (!this.ageData[0].img) {
				this.toast('请上传营业执照');
				return;
			}
			if (!this.ageData[1].img) {
				this.toast('请上传公司Logo');
				return;
			}
			if (!this.ageData[2].img) {
				this.toast('请上传门店照片');
				return;
			}
			if (!this.ageData[3].img) {
				this.toast('请上传装修案例');
				return;
			}
			postShop(this.list).then(res => {
				uni.showToast({
					title: '提交成功',
					icon: 'success',
					duration: 2000
				});
				setTimeout(function() {
					uni.switchTab({
						url: HOME
					});
				}, 2000);
			});
		},
		// 选择照片
		chooseimage: function(e) {
			var _this = this;
			uni.chooseImage({
				count: 9, // 默认9
				sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success: function(res) {
					// 返回选定照片的本地文件路径列表，marryPath可以作为img标签的src属性显示图片

					var imgFiles = res.tempFilePaths[0];

					console.log(res);
					uni.uploadFile({
						// 需要上传的地址
						url: UPLOADPHOTO,
						// filePath  需要上传的文件
						filePath: imgFiles,
						header: {
							'Content-Type': 'multipart/form-data',

							'Content-Type': 'application/json'
						},
						name: 'file',
						success: res1 => {
							// 显示上传信息
							console.log(res1);
							// console.log(_self)
							// console.log(e)
							// console.log(_self.dataList[e])
							// console.log(res1.data )
							// 									if(e == 3){

							// 										let w=JSON.parse(res1.data)
							// 										_self.photoData[0].img = imgFiles
							// 										_self.list.case =[]
							// 										_self.list.case.push({
							//     "caseName":'',
							//     "caseShowImg":w.data,
							//     "caseDescribe":''
							// })
							// 									} else{
							// 										let r =['businessImg','logo','storePhotos','details']

							// 										let w=JSON.parse(res1.data)
							// 										_self.ageData[e].img = imgFiles
							// 										_self.list[r[e]] = w.data
							// 									}
							let r = ['businessImg', 'logo', 'storePhotos', 'details'];

							let w = JSON.parse(res1.data);
							_self.ageData[e].img = imgFiles;
							_self.list[r[e]] = w.data;

							// if(e ==='thisRunningWater'||e ==='spouseRunningWater') {
							// 	_self.dataL[e].push(imgFiles);
							// 	_self.dataList[e].push(w.data);

							// } else{
							// 	_self.dataList[e] = w.data;
							// 	_self.dataL[e] = imgFiles;
							// }
						}
					});
				}
			});
		}
	},
	async onLoad(options) {
		_self = this;
		let e = JSON.parse(decodeURIComponent(options.data));
		_self.list = e;
	}
};
